<template>
  <div>
    <mu-appbar style="width: 100%;" class="header backgroundNaviBar">
      <a @click="()=>{
          $router.go(-1)
        }">
        <mu-button icon slot="left" class="icon" size="20">
          <mu-icon value="keyboard_arrow_left"></mu-icon>
        </mu-button>
      </a>
      <span style="color:white">{{title}}</span>
    </mu-appbar>
    <div class="body">
      <div class="paddingBox">
        <div class="flexbox backgroundColor heightBox">
          <div style="flex:2">
            <span>时间：</span>
          </div>
          <div style="flex:5" @click="selectTime">
            <!-- <span></span> -->
            <input type="text" class="input" disabled placeholder="选择提交时间" v-model="submitTime" />
          </div>
          <div style="flex:1">
            <img src alt />
          </div>
        </div>
        <div class="backgroundColor flexbox heightBox">
          <div style="flex:2">
            <span>操作人：</span>
          </div>
          <div style="flex:5">
            <!-- <span></span> -->
            <input type="text" class="input" placeholder="输入提交人姓名" v-model="person" />
          </div>
          <div style="flex:1">
            <img src alt />
          </div>
        </div>
        <div class="backgroundColor flexbox heightBox">
          <div>
            <span>原因：</span>
          </div>
        </div>
        <div
          class="backgroundColor"
          style="height:100px;padding:10px;border-bottom:1px rgb(238, 238, 238) solid;"
        >
          <div style="flex:5">
            <!-- <span></span> -->
            <!-- <input type="text" class="input" placeholder="输入简短的描述" /> -->
            <textarea name id="textarea" placeholder="输入简短的描述" v-model="reason"></textarea>
          </div>
        </div>
        <div class="backgroundColor flexbox heightBox">
          <div>
            <span>附件（图片）：</span>
          </div>
        </div>
        <div class="backgroundColor flexbox" style="padding:10px;">
          <div>
            <van-uploader v-model="fileList" multiple :after-read="beforeRead" />
          </div>
        </div>
      </div>
    </div>
    <van-popup v-model="show" position="bottom">
      <van-datetime-picker
        v-model="currentDate"
        type="datetime"
        :min-date="minDate"
        :max-date="maxDate"
        @confirm="confirmTime"
        @cancel="backbutton"
      />
    </van-popup>
    <div class="bottomSubmit">
      <div class="btnSubmit backgroundNaviBar" @click="submit()">
        <span>提交</span>
      </div>
    </div>
  </div>
</template>
<script>
import Vue from "vue";
import { Uploader } from "vant";
import AppBace from "../../../static/ajax";
import { DatetimePicker } from 'vant';

Vue.use(DatetimePicker);
Vue.use(Uploader);
export default {
  data() {
    return {
      minDate: new Date(2019, 0, 1),
      maxDate: new Date(2025, 10, 1),
      currentDate: new Date(),
      title: "",
      formdata: new FormData(),
      token: "",
      submitTime: "",
      person: "",
      fileList: [
        // { url: "https://img.yzcdn.cn/vant/leaf.jpg" }
        // Uploader 根据文件后缀来判断是否为图片文件
        // 如果图片 URL 中不包含类型信息，可以添加 isImage 标记来声明
      ],
      reason: "",
      show: false
    };
  },
  created() {
    this.token = sessionStorage.getItem("token");
    var status = this.$route.query.status;
    switch (status) {
      case 0:
        this.title = "停机操作";
        break;

      default:
        this.title = "恢复操作";
        break;
    }
  },
  methods: {
   
    confirmTime(val) {
      this.submitTime = AppBace.filterTime(val);
      this.show = false;
    },
    backbutton() {
      this.show = false;
    },
    selectTime() {
      this.show = true;
    },
    beforeRead(file) {
      // debugger
      this.formdata.append("files", file.file);
    },
    async submit() {
      var title = this.$route.query.title;
      var url = "/job/operate/device";
      this.formdata.append("gmtOperate", this.submitTime);
      this.formdata.append("person", this.person);
      this.formdata.append(
        "pid",
        this.title === "停机操作" ? null : Number(this.$route.query.id)
      );
      this.formdata.append("reason", this.reason);
      this.formdata.append("station", title);
      this.formdata.append("type", this.title === "停机操作" ? 0 : 1);
      // this.formdata.append("type",file.file);
     
      var res = await AppBace.request(url, "POST", this.formdata, this.token);
      console.log(res);
    }
  }
};
</script>
<style  scoped>
.bottomSubmit {
  display: flex;
  justify-content: center;
}
.backgroundColor {
  background-color: white;
}
.flexbox {
  display: flex;
  flex-direction: row;
}
.input {
  width: 100%;
  background-color: white;
  border: none;
}
.paddingBox {
  padding: 10px;
}
.heightBox {
  height: 50px;
  align-items: center;
  padding: 0 10px;
  border-bottom: 1px solid rgb(238, 238, 238);
}
#textarea {
  width: 100%;
  height: 80px;
  border: rgb(235, 234, 234) 1px solid;
  border-radius: 10px;
}
.btnSubmit {
  height: 40px;
  color: white;
  border-radius: 10px;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 96%;
  margin-top: 10px;
}
</style>


